import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Button,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Card,
  CardContent,
  Stack,
  CssBaseline,
  Alert
} from '@mui/material';
import {
  Save as SaveIcon,
  ArrowBack as ArrowBackIcon
} from '@mui/icons-material';
import AppTheme from '../shared-theme/AppTheme';
import ColorModeSelect from '../shared-theme/ColorModeSelect';

export default function BoardWrite() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    title: '',
    content: '',
    category: '',
    author: '' //     
  });
  const [errors, setErrors] = useState({});
  const [showSuccess, setShowSuccess] = useState(false);

  const categories = ['', '', '', '', ''];

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    //  
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};

    if (!formData.title.trim()) {
      newErrors.title = ' ';
    } else if (formData.title.length > 100) {
      newErrors.title = ' 100  ';
    }

    if (!formData.content.trim()) {
      newErrors.content = ' ';
    } else if (formData.content.length < 10) {
      newErrors.content = ' 10  ';
    }

    if (!formData.category) {
      newErrors.category = ' ';
    }

    return newErrors;
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    
    const newErrors = validateForm();
    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }

    //    
    console.log(' :', formData);
    
    setShowSuccess(true);
    setTimeout(() => {
      navigate('/board');
    }, 1500);
  };

  const handleCancel = () => {
    if (formData.title || formData.content) {
      if (window.confirm('   .  ?')) {
        navigate('/board');
      }
    } else {
      navigate('/board');
    }
  };

  if (showSuccess) {
    return (
      <AppTheme>
        <CssBaseline enableColorScheme />
        <Container maxWidth="md" sx={{ py: 8 }}>
          <Box sx={{ textAlign: 'center' }}>
            <Alert severity="success" sx={{ mb: 3 }}>
                !
            </Alert>
            <Typography variant="h6">
                 ...
            </Typography>
          </Box>
        </Container>
      </AppTheme>
    );
  }

  return (
    <AppTheme>
      <CssBaseline enableColorScheme />
      <ColorModeSelect sx={{ position: 'fixed', top: '1rem', right: '1rem' }} />
      
      <Container maxWidth="md" sx={{ py: 8 }}>
        {/*  */}
        <Box sx={{ mb: 4 }}>
          <Button
            startIcon={<ArrowBackIcon />}
            onClick={handleCancel}
            sx={{ mb: 2 }}
          >
             
          </Button>
          <Typography variant="h3" component="h1" gutterBottom>
              
          </Typography>
          <Typography variant="body1" color="text.secondary">
                 
          </Typography>
        </Box>

        {/*   */}
        <Card>
          <CardContent sx={{ p: 4 }}>
            <Box component="form" onSubmit={handleSubmit} noValidate>
              <Stack spacing={3}>
                {/*   */}
                <FormControl fullWidth error={!!errors.category}>
                  <InputLabel></InputLabel>
                  <Select
                    name="category"
                    value={formData.category}
                    onChange={handleChange}
                    label=""
                  >
                    {categories.map((category) => (
                      <MenuItem key={category} value={category}>
                        {category}
                      </MenuItem>
                    ))}
                  </Select>
                  {errors.category && (
                    <Typography variant="caption" color="error" sx={{ mt: 0.5, ml: 2 }}>
                      {errors.category}
                    </Typography>
                  )}
                </FormControl>

                {/*   */}
                <TextField
                  name="title"
                  label=""
                  value={formData.title}
                  onChange={handleChange}
                  error={!!errors.title}
                  helperText={errors.title || `${formData.title.length}/100`}
                  fullWidth
                  required
                  placeholder="  "
                />

                {/*   */}
                <TextField
                  name="content"
                  label=""
                  value={formData.content}
                  onChange={handleChange}
                  error={!!errors.content}
                  helperText={errors.content || `${formData.content.length}`}
                  fullWidth
                  multiline
                  rows={12}
                  required
                  placeholder="  ..."
                />

                {/*   */}
                <Box sx={{ p: 2, bgcolor: 'grey.100', borderRadius: 1 }}>
                  <Typography variant="body2" color="text.secondary">
                    : <strong>{formData.author}</strong>
                  </Typography>
                </Box>

                {/*  */}
                <Stack direction="row" spacing={2} justifyContent="flex-end">
                  <Button
                    type="button"
                    variant="outlined"
                    onClick={handleCancel}
                    size="large"
                  >
                    
                  </Button>
                  <Button
                    type="submit"
                    variant="contained"
                    startIcon={<SaveIcon />}
                    size="large"
                    disabled={!formData.title.trim() || !formData.content.trim() || !formData.category}
                  >
                     
                  </Button>
                </Stack>
              </Stack>
            </Box>
          </CardContent>
        </Card>

        {/*   */}
        <Box sx={{ mt: 4, p: 3, bgcolor: 'primary.50', borderRadius: 2 }}>
          <Typography variant="h6" gutterBottom>
              
          </Typography>
          <Typography variant="body2" component="div">
                <br/>
                  <br/>
               <br/>
                 
          </Typography>
        </Box>
      </Container>
    </AppTheme>
  );
}